os, lagos y océanos, la presencia microbiana está asegurada y es diversa; dentro de esta variedad, se encuentran bacterias, virus, hongos y protozoarios patógenos, es decir, microorganismos que pueden causar enfermedades a partir del consumo o contacto con esta agua.")]),_c('div',{staticClass:"cajon color-primario p-4 mb-3",staticStyle:{"background-color":"#FFECD5"}},[_c('p',{staticClass:"mb-0 fw-bold",attrs:{"data-aos":"fade-left"}},[_vm._v("La presencia de un amplio espectro de microorganismos patógenos es más probable en aguas residuales, debido a que muchos agentes microbianos que ocasionan enfermedades en humanos son liberados a través de excretas de personas infectadas que normalmente van a las aguas residuales.")])]),_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-left"}},[_vm._v("El análisis microbiológico en las aguas residuales es importante durante la implementación de sistemas de tratamiento de aguas, así como en la evaluación de la eficacia de los mismos. La determinación de microorganismos a la entrada de los sistemas de tratamiento de agua residual permitirá evaluar la efectividad del tratamiento en su reducción o ayudará a evaluar la posibilidad de utilizar estos microorganismos en procesos de biorremediación de las aguas. Asimismo, el análisis de la calidad microbiológica del agua luego de ser sometida a tratamientos es de importancia para medir la eficacia de los tratamientos de descontaminación empleados en las plantas de tratamiento de aguas residuales.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-8 ps-lg-4",attrs:{"data-aos":"fade-right"}},[_c('p',[_vm._v("Una de las variables más importantes de determinar en las aguas residuales son los parámetros microbiológicos, de acuerdo con la Resolución 0631 (2015); para los vertimientos puntuales de aguas residuales, tanto domésticas como no domésticas, se deben reportar los “valores de la concentración en Número Más Probable (NMP/100 mL) de los Coliformes Termotolerantes presentes en los vertimientos puntuales de aguas residuales (ARD y ARND), mediante las cuales se gestionan excretas humanas y/o de animales a cuerpos de aguas superficiales, cuando la carga másica en las aguas residuales antes del sistema de tratamiento es mayor a 125,00 Kg/día de DBO5” (p.4). Es decir, debe existir una relación entre los reportes de los parámetros fisicoquímicos y microbiológicos.")])]),_c('div',{staticClass:"col-lg-4"},[_c('img',{staticClass:"mb-4 mb-lg-auto mx-auto",staticStyle:{"width":"400px"},attrs:{"src":require("@/assets/curso/temas/tema2/img-12.png"),"data-aos":"fade-left","alt":"Imagen decorativa"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-edit mb-5",attrs:{"data-aos":"fade-left"}},[_c('h3',[_vm._v("Reutilización de las aguas residuales")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-flex flex-wrap align-items-center mb-5"},[_c('img',{staticClass:"mb-4 mb-lg-auto col-lg-4 mx-auto",staticStyle:{"max-width":"400px"},attrs:{"src":require("@/assets/curso/temas/tema2/img-13.png"),"alt":"Imagen decorativa","data-aos":"fade-right"}}),_c('div',{staticClass:"col-lg-8 ps-md-4",attrs:{"data-aos":"fade-left"}},[_c('p',[_vm._v("Actualmente, la reutilización de las aguas residuales en la agricultura, acuicultura y recarga de aguas subterráneas se ha convertido en una estrategia para abordar el problema de la escasez de las aguas dulces, debido al crecimiento demográfico, a la urbanización y al cambio climático. Asimismo, las aguas residuales, en algunos casos, pueden constituir el único recurso hídrico de las comunidades que subsisten por medio de la agricultura. Aunque la utilización de aguas residuales en la agricultura puede aportar beneficios, a su vez, representa un riesgo para la salud humana. Por ello, es necesario realizar controles estrictos a nivel microbiológico para disminuir los riesgos de salud pública que pueda traer este tipo de prácticas. Es así como el cumplimiento de ciertos límites y parámetros microbiológicos es fundamental para considerar la reutilización del agua tratada.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-8 ps-lg-4",attrs:{"data-aos":"fade-right"}},[_c('p',[_vm._v("Teniendo en cuenta que la diversidad microbiana en las aguas residuales es alta, puede ser difícil determinar la presencia de todos estos microorganismos; es por ello por lo que normalmente se establecen unos indicadores microbiológicos específicos para su determinación. Dentro de esos indicadores, los más utilizados son los coliformes totales y los coliformes fecales, dentro de los que se encuentra E. coli, pero, de acuerdo con expertos, este indicador no sería suficiente para determinar la calidad de las aguas residuales tratadas, debido a que, por ejemplo, la determinación de E.coli solo refleja la calidad bacteriana del agua, dejando de lado otros patógenos como virus y protozoos.")])]),_c('div',{staticClass:"col-lg-4"},[_c('img',{staticClass:"mb-4 mb-lg-auto mx-auto",staticStyle:{"width":"400px"},attrs:{"src":require("@/assets/curso/temas/tema2/img-14.png"),"data-aos":"fade-left","alt":"Imagen decorativa"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-sexto color-acento-contenido d-block"},[_c('h5',[_vm._v("Tabla 4.")]),_c('span',{staticClass:"mb-0"},[_vm._v("Microorganismos indicadores de calidad en aguas residuales")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tabla-a color-acento-contenido mb-5 tabla-b col-xl-10 mx-auto",attrs:{"data-aos":"fade-left"}},[_c('table',[_c('caption',[_c('b',[_vm._v("